<h2>属性</h2>
<h3><code v-pre="">vc-geometry-plane</code></h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定顶点坐标渲染类型。</td>
</tr>
</tbody>
</table>
<hr>
<h3><code v-pre="">vc-geometry-outline-plane</code></h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody></tbody>
</table>
<hr>
<p>参考官方文档：<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/PlaneGeometry.html">PlaneGeometry</a></strong>、 <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/PlaneOutlineGeometry.html">PlaneOutlineGeometry</a></strong></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          appearanceOutline: null,
          geometry: null,
          attributes: null,
          vertexFormat: null,
          modelMatrix: null,
          attributesOutline: null
        }
      },
      mounted() {
        Promise.all([this.$refs.plane.createPromise, this.$refs.planeOutline.createPromise]).then((instances) => {
          console.log('All geometries are loaded.')
          const boundingSphereUnion = instances.reduce((prev, cur) => {
            const geometry = cur.cesiumObject.constructor.createGeometry(cur.cesiumObject)
            const boundingSphere = cur.vm.$parent.modelMatrix
              ? Cesium.BoundingSphere.transform(geometry.boundingSphere, cur.vm.$parent.modelMatrix)
              : geometry.boundingSphere
            return prev === null ? boundingSphere : Cesium.BoundingSphere.union(prev, boundingSphere)
          }, null)
          instances[0].viewer.scene.camera.flyToBoundingSphere(boundingSphereUnion)
        })
      },
      methods: {
        ready({ Cesium, viewer }) {
          this.viewer = viewer
          const { PerInstanceColorAppearance, Cartesian3, ColorGeometryInstanceAttribute, Matrix4, Transforms } = Cesium
          this.appearance = new PerInstanceColorAppearance({
            closed: true
          })
          this.appearanceOutline = new PerInstanceColorAppearance({
            flat: true,
            renderState: {
              lineWidth: Math.min(2.0, viewer.scene.maximumAliasedLineWidth)
            }
          })
          this.attributes = {
            color: new ColorGeometryInstanceAttribute(Math.random(), Math.random(), Math.random(), 0.5)
          }
          this.attributesOutline = {
            color: new ColorGeometryInstanceAttribute(Math.random(), Math.random(), Math.random())
          }
          this.vertexFormat = PerInstanceColorAppearance.VERTEX_FORMAT
          const dimensions = new Cartesian3(400000.0, 300000.0, 1.0)
          const translateMatrix = Transforms.eastNorthUpToFixedFrame(Cartesian3.fromDegrees(108, 38))
          const scaleMatrix = Matrix4.fromScale(dimensions)
          const planeModelMatrix = new Matrix4()
          this.modelMatrix = Matrix4.multiply(translateMatrix, scaleMatrix, planeModelMatrix)
        },
        LEFT_CLICK(movement) {
          const feature = this.viewer.scene.pick(movement.position)
          console.log(feature)
        }
      }
    }
  </script>